interface HeaderProps extends PropsWithChildren {
    title?: string;
};

const Header: React.FC<HeaderProps> = ({
    title = "Luxe Diam",
    children
}) => {
    const theme = useTheme();
    const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
    const [scrollY, setScrollY] = useState(0);
    const [open, setOpen] = useState(false);
    const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);

    const handleScroll = () => {
        setScrollY(window.scrollY);
    };

    const handleToggleOpen = () => {
        setOpen(!open);
    };

    React.useEffect(() => {
        window.addEventListener('scroll', handleScroll);

        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);

    return (
        <AppBar
            position="static"
            sx={{
                bgcolor: 'background.paper',
                color: theme.palette.text.primary,
                boxShadow: (scrollY > 0 || open) ? theme.shadows[3] : 'none',
                height: "60px",
                top: 0
            }}
        >

            <Toolbar component={Container} sx={{ justifyContent: 'space-between' }}>

                <AnimatedTypography variant="h6" color="inherit">
                    {title}
                </AnimatedTypography>

                <Stack direction="row" gap={1} >
                    <IconButton onClick={(e) => {
                        handleToggleOpen();
                        setAnchorEl(e.currentTarget);
                    }} >
                        <Avatar src="/logo192.png" />
                    </IconButton>
                    {/* {isMobile && (
                        <BurgerMenu open={open} onClick={handleToggleOpen} >
                            <span></span>
                            <span></span>
                            <span></span>
                        </BurgerMenu>
                    )} */}

                    <Menu
                        sx={{
                            "& .MuiMenu-paper": {
                                backgroundColor: theme.palette.background.paper,
                                color: theme.palette.text.primary,
                                boxShadow: theme.shadows[7],
                                width: {
                                    xs: "90vw",
                                    md: "40vw"
                                },
                                height: {
                                    xs: "90vh",
                                    md: "70vh"
                                },
                                borderRadius: "21px",
                                padding: "1em",
                                overflowX: "hidden",
                                overflowY: "scroll"
                            }
                        }}
                        anchorEl={anchorEl} open={open} onClose={() => { handleToggleOpen(); setAnchorEl(null); }} >
                        <Auth next={() => {
                            handleToggleOpen();
                            setAnchorEl(null);
                        }} />
                    </Menu>
                </Stack>

            </Toolbar>

            {isMobile && (
                <Collapse
                    in={open}
                    sx={{ backgroundColor: theme.palette.background.paper }}>
                    <Box sx={{ padding: "2em" }} >
                        <Stack gap={1} mt={2} >
                            {children}
                        </Stack>
                    </Box>
                </Collapse>
            )}

        </AppBar>
    );
};

export default Header;
