import React from 'react';

// mui
import { Box, CircularProgress, IconButton, Menu, Skeleton, Slider, SliderProps, Stack, Tooltip, Typography, styled, useTheme } from '@mui/material';

// custom components
import Diamond from '../components/Jewelery/Diamond';

// react relay & query types
import { usePaginationFragment } from 'react-relay';
import { DiamondListPaginationQuery$variables } from './__generated__/DiamondListPaginationQuery.graphql';
import { DiamondListQuery_diamonds$key } from './__generated__/DiamondListQuery_diamonds.graphql';

// svg types and components
import Shapes, { ClarityChoicesKeys, ColorChoicesKeys, GradeChoicesKeys, ShapeKeys } from '../svgs/shapes';
import { ChevronLeft, ChevronRight } from '@mui/icons-material';
import { slideInFromTop } from '../animations/text';
import Hamburger from '../components/Hamburger';

// gql constants
const graphql = require('babel-plugin-relay/macro');

interface DiamondListProps {
    diamonds: DiamondListQuery_diamonds$key;
};

const query = graphql`
    fragment DiamondListQuery_diamonds on Query
        @refetchable(queryName: "DiamondListPaginationQuery")
        @argumentDefinitions(
            first: { type: "Int", defaultValue: 10 }
            after: { type: "String" },
            caratGte: { type: "Float" },
            caratLt: { type: "Float" },
            shapeLte: { type: "String" },
            shapeGte: { type: "String" },
            colorLte: { type: "String" },
            colorGte: { type: "String" },
            cutLte: { type: "String" },
            cutGte: { type: "String" },
            polishLte: { type: "String" },
            polishGte: { type: "String" },
            symmetryLte: { type: "String" },
            symmetryGte: { type: "String" },
            flourescenceIntensityLte: { type: "String" },
            flourescenceIntensityGte: { type: "String" },
            clarityLte: { type: "String" },
            clarityGte: { type: "String" },
            depthLte: { type: "Float" },
            depthGte: { type: "Float" },
        )
    {
        diamonds(
            first: $first,
            after: $after,
            caratWeight_Gte: $caratGte,
            caratWeight_Lt: $caratLt,
            shape_Lte:$shapeLte,
            shape_Gte: $shapeGte,
            color_Lte: $colorLte,
            color_Gte: $colorGte,
            cut_Lte: $cutLte,
            cut_Gte: $cutGte,
            polish_Lte: $polishLte,
            polish_Gte: $polishGte,
            symmetry_Lte: $symmetryLte,
            symmetry_Gte: $symmetryGte,
            flourescenceIntensity_Lte: $flourescenceIntensityLte,
            flourescenceIntensity_Gte: $flourescenceIntensityGte,
            clarity_Lte: $clarityLte,
            clarity_Gte: $clarityGte,
            depth_Lte: $depthLte,
            depth_Gte: $depthGte,
        )
        @connection(key: "DiamondListQuery_diamonds")
        {
            edges {
                node {
                    id
                    ...DiamondList_data
                }
            }
        }
    }
`;

function Cards({ diamonds, filters, open }: { open: boolean, diamonds: DiamondListQuery_diamonds$key, filters: DiamondListPaginationQuery$variables }) {
    const {
        data,
        loadNext,
        loadPrevious,
        hasNext,
        hasPrevious,
        isLoadingNext,
        isLoadingPrevious,
        refetch, // For refetching connection
    } = usePaginationFragment(
        query,
        diamonds
    );

    React.useEffect(() => {
        if (open === false) { refetch(filters); }
    }, [open, filters]);

    return (
        <>
            {data?.diamonds?.edges?.map(
                (edge, index) => {
                    return edge?.node ? (
                        <Diamond
                            data={edge.node}
                            key={index}
                        />
                    ) : null
                }
            )}

            <Box
                sx={{
                    position: 'sticky',
                    bottom: 10,
                    width: "100%",
                    height: 100,
                    p: 1,
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    "& .MuiStack-root": {
                        borderRadius: 50,
                        overflow: 'hidden',
                        bgcolor: 'background.paper',
                        boxShadow: 1,
                        width: 'auto',
                        height: 'auto',
                    }
                }}
            >

                <Stack
                    direction="row"
                    spacing={2}
                    sx={{
                        justifyContent: 'space-between',
                        alignItems: 'center',
                        height: 100,

                        "& .MuiIconButton-root": {
                            p: 1,
                            borderRadius: "50%",
                            transition: 'scale 300ms ease-in-out',
                            "&:hover": {
                                bgcolor: "background.paper",
                                color: "primary.main",
                                scale: 1.1
                            }
                        }
                    }}
                >
                    <IconButton
                        disabled={!hasPrevious || isLoadingPrevious}
                        onClick={() => { loadPrevious(10); }}
                    >
                        {!isLoadingPrevious ? <ChevronLeft /> : <CircularProgress />}
                    </IconButton>
                    <Typography variant="h5" fontWeight="400" component="div" gutterBottom p={2}>
                        {data?.diamonds?.edges?.length ?? 0} / {data?.diamonds?.edges?.length ?? 0}
                    </Typography>
                    <IconButton
                        disabled={!hasNext || isLoadingNext}
                        onClick={() => { loadNext(10); }}
                    >
                        {!isLoadingNext ? <ChevronRight /> : <CircularProgress />}
                    </IconButton>
                </Stack>

            </Box>

        </>
    )
};

const FilterSliderBox = styled(Box)(({ theme }) => ({
    width: '95%',
    alignItems: 'center',
    justifyContent: 'center',
    alignSelf: 'center',
    marginTop: "10px",
    marginBottom: "10px",
    "& .MuiSlider-root": {
        width: '100%',
        color: theme.palette.primary.main,
        "& .MuiSlider-thumb": {
            bgcolor: theme.palette.background.paper,
        },
        "& .MuiSlider-track": {
            bgcolor: theme.palette.background.default,
        }
    },

    "& .MuiTypography-root": {
        width: '100%',
        textAlign: "left",
        fontWeight: "400",
        color: theme.palette.text.secondary,
        fontSize: "1rem",
    }
}));

const FilterSliderOptions = (
    { label, sliderProps, choices }: {
        label: string,
        sliderProps: SliderProps,
        choices: string[]
    }
) => {
    return (
        <FilterSliderBox>
            <Typography variant="h5">
                {label} :
                {" "}{choices[Array.isArray(sliderProps?.value) ? sliderProps?.value[0] : 0]}{" - "}
                {choices[Array.isArray(sliderProps?.value) ? sliderProps?.value[1] : choices.length - 1]}
            </Typography>
            <Slider
                min={0}
                max={choices.length - 1}
                step={1}
                valueLabelDisplay="off"
                marks={Array(choices.length).fill(" ").map((_, index) => ({
                    value: index,
                    label: choices[index]
                }))}
                sx={{

                }}
                {...sliderProps}
            />
        </FilterSliderBox>
    )
};

function DiamondList(props: DiamondListProps) {
    const theme = useTheme();
    const [filter, setFilter] = React.useState<DiamondListPaginationQuery$variables>({
        "first": 10,
        "after": null,
        caratGte: 0.1,
        caratLt: 2.5,
        shapeLte: "7",
        shapeGte: "0",
        colorLte: "4",
        colorGte: "0",
        cutLte: "4",
        cutGte: "0",
        polishLte: "4",
        polishGte: "0",
        symmetryLte: "4",
        symmetryGte: "0"
    });
    const _render_shape_filter = (key: string, index: number) => {
        const isSelected = filter?.shapeGte === index?.toString() || filter?.shapeLte === index?.toString();
        const inBetween = filter?.shapeGte && filter?.shapeLte && index > parseInt(filter?.shapeGte) && index < parseInt(filter?.shapeLte);
        const _changeShape = () => {
            if (isSelected) {
                setFilter({
                    ...filter,
                    shapeGte: null,
                    shapeLte: null
                });
                return;
            }
            else if (filter?.shapeGte && parseInt(filter?.shapeGte) < index) {
                setFilter({
                    ...filter,
                    shapeLte: index?.toString()
                });
                return;
            }
            else if (filter?.shapeLte && parseInt(filter?.shapeLte) > index) {
                setFilter({
                    ...filter,
                    shapeGte: index?.toString()
                });
                return;
            }
            else {
                setFilter({
                    ...filter,
                    shapeGte: index?.toString()
                });
                return;
            }
        };

        return (
            <Tooltip title={key} key={index}>
                <IconButton>
                    <Shapes
                        onClick={_changeShape}
                        shape={key as any}
                        width={50}
                        height={50}
                        sx={{
                            fill: isSelected ?
                                theme.palette.primary.main :
                                inBetween ? theme.palette.text.primary
                                    : theme.palette.text.secondary
                        }}
                    />
                </IconButton>
            </Tooltip>
        );

    };
    const [filterDrawerAnchor, setFilterDrawerAnchor] = React.useState<null | HTMLElement>(null);
    const open = Boolean(filterDrawerAnchor);
    const toggleFilterDrawer = (e: any) => {
        if (open) {
            setFilterDrawerAnchor(null);
        }
        else {
            setFilterDrawerAnchor(e.currentTarget);
        }
    };

    const isMobile = window.innerWidth < theme.breakpoints.values.sm;

    const FilterDrawer = (
        <Menu
            open={open}
            anchorEl={filterDrawerAnchor}
            onClose={toggleFilterDrawer}
            aria-controls={open ? 'filter-menu' : undefined}
            aria-haspopup="true"
            aria-expanded={open ? 'true' : undefined}
            sx={{
                maxHeight: {
                    xs: "100%",
                    md: "80vh"
                },
                top: 10,
                justifyContent: "center",
                "& .MuiPaper-root": {
                    paddingBottom: 5,
                    px: 2,
                    maxWidth: {
                        xs: "90%",
                        sm: "70%",
                        md: "40%",
                    },
                    borderRadius: "21px",
                    '&::-webkit-scrollbar': {
                        display: "none"
                    },
                    opacity: open ? 1 : 0,
                    animation: `${slideInFromTop} 500ms ease-in`
                },
                "& .MuiList-root": {
                    px: "5px",
                    width: '100%',
                    alignSelf: "center"
                },
                visibility: open ? "visible" : "hidden"
            }}
        >

            <Typography variant="h4" textAlign="center" sx={{ textTransform: "uppercase" }} color="primary.main"  >
                Filters
            </Typography>

            {/* shape filter */}
            <FilterSliderBox>
                <Typography variant="h5">
                    Shapes :
                    {" "}{filter?.shapeGte ? ShapeKeys[parseInt(filter?.shapeGte)] : " - "}{" - "}
                    {filter?.shapeLte ? ShapeKeys[parseInt(filter?.shapeLte)] : " - "}
                </Typography>
                <Box
                    sx={{
                        display: 'flex',
                        width: '100%',
                        overflowX: "scroll",
                        overflowY: "hidden",
                        alignItems: 'center',
                        height: 100,
                        scrollbarWidth: "50px",
                        scrollBehavior: "smooth",
                        "&::-webkit-scrollbar": {
                            bgcolor: "background.default",
                            borderRadius: "2px",
                            alignSelf: "center",
                            height: "5px",
                        },
                        "&::-webkit-scrollbar-thumb": {
                            bgcolor: "primary.main",
                            borderRadius: "2px",
                            width: "4px",
                            height: "2.5px",
                            my: "1.25px",
                            transition: "scale 300ms ease"
                        },
                        "&::-webkit-scrollbar-thumb:hover": {
                            scale: 1.1
                        }
                    }}
                    children={
                        <Stack
                            direction="row"
                            spacing={2}
                            children={ShapeKeys.map(_render_shape_filter)}
                        />
                    }
                />
            </FilterSliderBox>
            {/*  */}


            {/* carat weight filter */}
            <FilterSliderBox>
                <Typography variant="h5">
                    Carat Weight :
                    {" "}{filter?.caratGte ? filter?.caratGte : " - "}{" - "}
                    {filter?.caratLt ? filter?.caratLt : " - "}
                </Typography>
                <Slider
                    value={[filter?.caratGte ? filter?.caratGte : 0, filter?.caratLt ? filter?.caratLt : 0]}
                    onChange={(e, v) => {
                        // @ts-ignore
                        setFilter({ ...filter, caratGte: v[0] as any ?? 0, caratLt: v[1] as any ?? 0 });
                    }}
                    step={0.01}
                    min={0}
                    max={10}
                    marks={Array(10).map((_, index) => ({
                        value: index,
                        label: index
                    }))}
                />
            </FilterSliderBox>
            {/*  */}

            {/* color filter */}
            <FilterSliderOptions
                label="Color"
                choices={isMobile ? ColorChoicesKeys.slice(0, 7) : ColorChoicesKeys}
                sliderProps={{
                    value: [
                        filter?.colorGte ? parseInt(filter?.colorGte) : 0,
                        filter?.colorLte ? parseInt(filter?.colorLte) : 0
                    ],
                    onChange: (e, v) => {
                        // @ts-ignore
                        setFilter({ ...filter, colorGte: v[0] as string ?? "0", colorLte: v[1] as string ?? "0" });
                    }
                }}
            />
            {/*  */}

            {/* clarity filter */}
            <FilterSliderOptions
                label="Clarity"
                choices={isMobile ? ClarityChoicesKeys.slice(1, 8) : ClarityChoicesKeys}
                sliderProps={{
                    value: [
                        filter?.clarityGte ? parseInt(filter?.clarityGte) : 0,
                        filter?.clarityLte ? parseInt(filter?.clarityLte) : 1
                    ],
                    onChange: (e, v) => {
                        // @ts-ignore
                        setFilter({ ...filter, clarityGte: v[0] as string ?? "0", clarityLte: v[1] as string ?? "0" });
                    }
                }}
            />
            {/*  */}

            {/* cut Filter */}
            <FilterSliderOptions
                label="Cut"
                choices={GradeChoicesKeys}
                sliderProps={{
                    value: [
                        filter?.cutGte ? parseInt(filter?.cutGte) : 0,
                        filter?.cutLte ? parseInt(filter?.cutLte) : 1
                    ],
                    onChange: (e, v) => {
                        // @ts-ignore
                        setFilter({ ...filter, cutGte: v[0] as string ?? "0", cutLte: v[1] as string ?? "0" });
                    }
                }}
            />
            {/*  */}

            {/* polish Filter */}
            <FilterSliderOptions
                label="Polish"
                choices={GradeChoicesKeys}
                sliderProps={{
                    value: [
                        filter?.polishGte ? parseInt(filter?.polishGte) : 0,
                        filter?.polishLte ? parseInt(filter?.polishLte) : 1
                    ],
                    onChange: (e, v) => {
                        // @ts-ignore
                        setFilter({ ...filter, polishGte: v[0] as string ?? "0", polishLte: v[1] as string ?? "0" });
                    }
                }}
            />
            {/*  */}

            {/* symmetry Filter */}
            <FilterSliderOptions
                label="Symmetry"
                choices={GradeChoicesKeys}
                sliderProps={{
                    value: [
                        filter?.symmetryGte ? parseInt(filter?.symmetryGte) : 0,
                        filter?.symmetryLte ? parseInt(filter?.symmetryLte) : 1
                    ],
                    onChange: (e, v) => {
                        // @ts-ignore
                        setFilter({ ...filter, symmetryGte: v[0] as string ?? "0", symmetryLte: v[1] as string ?? "0" });
                    }
                }}
            />
            {/*  */}

        </Menu>
    );

    return (
        <React.Fragment>

            <Typography variant="h3" fontWeight="400" component="div" gutterBottom p={2}>
                Browse Diamonds
            </Typography>

            <Stack
                sx={{
                    position: 'sticky',
                    top: 0,
                    width: "100%",
                    height: "70px",
                    px: {
                        xs: 2,
                        md: 5
                    },
                    display: 'flex',
                    backdropFilter: 'blur(10px)',
                    bgcolor: "background.paper",
                    zIndex: 99,
                    borderRadius: {
                        xs: 0,
                        sm: "12px"
                    },
                }}
                direction="row"
                justifyContent="space-between"
                alignItems="center"
            >
                {/* <Container> */}
                <Typography variant="h5" fontWeight="400"
                    sx={{ "em": { color: "text.secondary" } }}
                >
                    Filters <em> ({Object.keys(filter).length} applied)</em>
                </Typography>

                <Hamburger
                    open={open}
                    onClick={toggleFilterDrawer}
                >
                    <span />
                    <span />
                    <span />
                </Hamburger>

                {FilterDrawer}

            </Stack>

            <Box
                sx={{
                    display: 'flex',
                    width: '100%',
                    alignItems: 'center',
                    justifyContent: 'center',
                    flexWrap: "wrap",
                    flexDirection: "row",
                    gap: 1,
                }}
            >

                <React.Suspense
                    fallback={
                        <Skeleton
                            variant="rectangular"
                            width="100%"
                            height="80vh"
                        />
                    }
                >

                    <Cards diamonds={props.diamonds} filters={filter} open={open} />
                </React.Suspense>

            </Box>
        </React.Fragment>
    );

};

export default DiamondList;
