import React from 'react';

// MUI
import { Box, CircularProgress, Container, Theme, createTheme } from '@mui/material';

// react roouter
import {
    createBrowserRouter,
    Outlet,
    RouterProvider,
} from "react-router-dom";

// relay
import { RelayEnvironmentProvider, useLazyLoadQuery } from 'react-relay';
// environment  
import prodEnv, { devEnv } from '../relay/environment';

// theme
import ThemeWrapper from '../theme/Wrapper';

// screens
import DiamondList from './DiamondList';
import Diamond from "./DiamondDetails";

// relay compiler configuration
import { RootQuery } from './__generated__/RootQuery.graphql';
import Header from '../components/Header';

// graphql raw string
const graphql = require('babel-plugin-relay/macro');


// root query
const RQ = graphql`
    query RootQuery {
        ...DiamondListQuery_diamonds
    }
`;


interface RootProps {

};

const theme: Theme = createTheme({
    palette: {
        mode: 'dark',
        primary: {
            main: 'rgb(252,212,98)', // Primary color
        },
        secondary: {
            main: '#f50057', // Secondary color
        },
        "text": {
            primary: "rgba(255,255,255,1)",
            secondary: "rgb(163,163,163)",
        },
        "background": {
            default: "#1c1b19",
            // default: "#eee",
            paper: "#111"
        }
    },
    "typography": {
        fontFamily: "Roboto, sans-serif, Alex Brush, Rubik, Prata, serif"
    },
    components: {
        MuiPaper: {
            styleOverrides: {
                root: {
                    boxShadow: '0px 2px 6px rgba(255, 255, 255, 0.15)', // Paper shadow
                },
            },
        },
        MuiButton: {
            styleOverrides: {
                root: {
                    borderRadius: '4px', // Button border radius
                },
            },
        },
    },
});

const RootLayout = (props: any) => {
    return (
        <Box>
            <Header />
            <Container sx={{ px: { xs: 0 } }} >
                <React.Suspense
                    fallback={
                        <Box
                            sx={{
                                display: "flex",
                                justifyContent: "center",
                                alignItems: "center",
                                height: "100vh",
                                width: "100vw",
                            }}
                        >
                            <CircularProgress />
                        </Box>
                    }
                >
                    <Outlet />
                </React.Suspense>
            </Container>
        </Box>
    )
}

function LoadRoot(props: RootProps) {
    const data = useLazyLoadQuery<RootQuery>(
        RQ,
        {},
        { fetchPolicy: "store-and-network" }
    );

    return (
        <React.Suspense
            fallback={
                <Box
                    sx={{
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        height: "100vh",
                        width: "100vw",
                    }}
                >
                    <CircularProgress />
                </Box>
            }
        >
            <DiamondList diamonds={data} />
        </React.Suspense>
    )
};

const router = createBrowserRouter([
    {
        path: "/",
        element: <RootLayout />,
        children: [
            {
                path: "",
                element: <LoadRoot />
            },
            {
                path: "/diamond/:id",
                element: <Diamond />
            }
        ]
    },
]);

function Root(props: RootProps) {
    const e = true ?
        devEnv({
            "extendedUrl": "jewellery/graphql/",
            token: window.localStorage.getItem("token") ?? undefined
        })
        : prodEnv({
            // "apiToken": "03b5c990cf1907dde682a66887820be6c5b7ede11a848b641df3c02872404996",
            // jmc api
            "apiToken": "468da0934b3ab027cdc8d00a96c33f9b9fc57391556749d2c4cc584f0c378367",
            "url": "https://api.beyondigital.agency/jewellery/graphql/",
            token: window.localStorage.getItem("token") ?? undefined
        });


    return (
        <React.Suspense fallback={<div>Loading...</div>}>
            <RelayEnvironmentProvider environment={e} >
                <ThemeWrapper
                    darkTheme={theme}
                    lightTheme={theme}
                >

                    <Box
                        sx={{
                            height: "100vh",
                            flexDirection: "column",
                            width: "100vw",
                            backgroundImage: "url(/static/luxe/pattern-01.png)",
                            overflow: "hidden",
                            backgroundColor: "background.default",
                            backgroundSize: "cover",
                            backgroundPosition: "center",
                            position: "fixed",
                            bottom: 0,
                            left: 0,
                            right: 0,
                            top: 0,
                            zIndex: -1
                        }}
                    />
                    <RouterProvider router={router} />
                </ThemeWrapper>
            </RelayEnvironmentProvider>
        </React.Suspense>

    );
};

export default Root;
