export const shapes = {
    "ROUND": round,
    "RADIANT": radiant,
    "PRINCESS": princess,
    "PEAR": pear,
    "OVAL": oval,
    "MARQUISE": marquise,
    "HEART": heart,
    "EMERALD": emerald,
    "ASSCHER": asscher,
    "CUSHION": cushion,
    "FANCY": fancy,
};

const Svg = styled("svg")(({ theme }) => ({
    fill: theme.palette.text.primary,
}));

export default function Shapes(
    {
        shape = "ROUND",
        children,
        sx,
        ...props
    }: SVGProps<any> & { shape: keyof typeof shapes; sx?: SxProps }
) {
    return (
        <Svg
            viewBox="0 0 600 600"
            xmlns="http://www.w3.org/2000/svg"
            sx={sx}
            {...props}
        >
            {shapes[shape]}
        </Svg>
    )
};

