import React, { PropsWithChildren, useState } from 'react';
import { useLazyLoadQuery, useMutation, useRefetchableFragment } from 'react-relay';
import { DiamondDetailsQuery } from './__generated__/DiamondDetailsQuery.graphql';
import { Box, Button, CircularProgress, Collapse, Container, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Divider, Grid, IconButton, Skeleton, Slide, Stack, TextField, Typography } from '@mui/material';
import { ChevronRight, MessageSharp, Share } from '@mui/icons-material';
import { slideInFromTop } from '../animations/text';
import { TransitionProps } from '@mui/material/transitions';
import { DiamondDetailsInquiryMutation, DiamondDetailsInquiryMutation$variables } from './__generated__/DiamondDetailsInquiryMutation.graphql';
import { MobileInput } from './Auth';
import DiamondCard from '../components/Jewelery/Diamond';
import { DiamondDetailsSuggestMore$key } from './__generated__/DiamondDetailsSuggestMore.graphql';
import { DiamondDetailsSuggestMoreQuery$variables } from './__generated__/DiamondDetailsSuggestMoreQuery.graphql';
import { ClarityChoicesKeys, ColorChoicesKeys, GradeChoicesKeys, ShapeKeys } from '../svgs/shapes';
import { useParams } from 'react-router';

const graphql = require('babel-plugin-relay/macro');

interface DiamondDetailsProps {

}

interface DiamondDetailsState {
    openAdvance: boolean;
    openInquiryForm: boolean;
}
const Transition = React.forwardRef(function Transition(
    props: TransitionProps & {
        children: React.ReactElement<any, any>;
    },
    ref: React.Ref<unknown>,
) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const query = graphql`
    query DiamondDetailsQuery($id: ID!) {
        ...DiamondDetailsSuggestMore
        node(id: $id) {
            ... on DiamondType {
                        id
                        business{
                            id
                            displayName
                            zipcode
                        }
                        reportNumber
                        shape
                        srNo
                        caratWeight
                        dimensions
                        fluorescence
                        fluorescenceColour
                        fancy
                        fancyColour
                        fancyIntensity
                        isNatural
                        isLabGrown
                        isTreated
                        isIrradiated
                        isColorEnhanced
                            clarity
                        cut
                        color
                        polish
                        polish
                        width
                        height
                        length
                        crownHeight
                        depth
                        table
                        comment
                        girdleThickness
                        gridlePercentage
                        currency
                        price
                        inquiriesEnabled
                        pavilionAngle
                        lab
                        reportNumber
                        status
            }
        }
    }
`;

class DiamondDetails extends React.Component<DiamondDetailsProps, DiamondDetailsState> {

    constructor(props: DiamondDetailsProps) {
        super(props);
        this.state = {
            openAdvance: false,
            openInquiryForm: false
        };
    }

    SuggestMore = ({ frag, vars }:
        { frag: DiamondDetailsSuggestMore$key, vars: Partial<DiamondDetailsSuggestMoreQuery$variables> }
    ) => {
        const [data, refetch] = useRefetchableFragment(
            graphql`
                fragment DiamondDetailsSuggestMore on Query
                @refetchable(queryName: "DiamondDetailsSuggestMoreQuery")
                @argumentDefinitions(
                    first: { type: "Int", defaultValue: 4 },
                    after: { type: "String" },
                    caratGte: { type: "Float" },
                    caratLt: { type: "Float" },
                    shapeLte: { type: "String" },
                    colorLte: { type: "String" },
                    cutLte: { type: "String" },
                    clarityLte: { type: "String" },
                    fetchMore: { type: "Boolean", defaultValue: false }
                )
                {
                    diamonds(
                        first:$first,
                        after:$after,
                        caratWeight_Lt: $caratLt,
                        caratWeight_Gte: $caratGte,
                        clarity_Lte: $clarityLte,
                        color_Lte: $colorLte,
                        shape_Lte: $shapeLte,
                        cut_Lte: $cutLte,
                    )
                    @connection(key: "DiamonDetailsSuggestMoreQuery_diamonds")
                    @include(if: $fetchMore)
                {
                    edges{
                        node{
                            id
                            ...DiamondList_data
                        }
                    }
                }
            }
            `,
            frag
        );

        React.useEffect(() => {
            refetch(vars, {
                "fetchPolicy": "store-or-network"
            });
        }, [vars]);

        if (!data) {
            return null;
        }

        return (
            <Stack direction="column" gap={1} >
                <Typography
                    component="h1"
                    variant="h4"
                    children="You may also like"
                />
                <Box
                    sx={{
                        display: "flex",
                        flexWrap: "wrap",
                        justifyContent: "space-evenly",
                        alignItems: "center",
                        maxWidth: "100%"
                    }}
                >
                    <Stack direction="row" gap={1} >
                        {data?.diamonds?.edges?.map((edge: any) => {
                            return (<DiamondCard data={edge.node} />)
                        })}
                        {
                            data?.diamonds?.edges.length === 0 &&
                            <Typography
                                component="p"
                                variant="body1"
                                children="No Similar diamonds to show at the moment."
                            />
                        }
                    </Stack>
                </Box>
            </Stack>
        );

    }

    InquiryForm = () => {
        const { id } = useParams()
        const [input, setInput] = useState<Partial<DiamondDetailsInquiryMutation$variables["i"]>>({
            inquiringFor: id,
            subject: "Inquiry Regarding a Diamond",
            name: "",
            message: "",
            mobile: {
                countryCode: "+91",
                number: ""
            }
        });
        const { openInquiryForm } = this.state;
        const [commit, isInFlight] = useMutation<DiamondDetailsInquiryMutation>(
            graphql`
                mutation DiamondDetailsInquiryMutation($i:InquiryMutationInput!) {
                    inquiry(input:$i){
                        success
                        error
                    }
                }
            `
        );

        const handleClose = () => {
            this.setState({
                ...this.state,
                openInquiryForm: false,
            });
        };

        const _handle = () => {
            if (input.message && input.name && input.subject) {
                return commit({
                    variables: {
                        i: input as DiamondDetailsInquiryMutation$variables["i"]
                    },
                    onCompleted: (res, err) => {
                        if (res && res.inquiry?.success) {
                            handleClose();
                        }
                        else if (res.inquiry?.error) {
                            alert(res.inquiry?.error)
                        }
                    },
                    onError: (err) => {

                    }
                });
            }
            else {
                alert("Please provide all required Info to submit the form.")
            }

        };

        React.useEffect(() => {
            const enterL = (e: any) => {
                if (e.key === "Enter") {
                    _handle();
                }
            };

            window.addEventListener("keydown", enterL)
            return () => window.removeEventListener("keydown", enterL)
        },
            []
        )

        return (
            <Dialog
                open={openInquiryForm}
                TransitionComponent={Transition}
                keepMounted
                onClose={handleClose}
                aria-describedby="alert-dialog-slide-description"
                sx={{
                    "& .MuiPaper-root": {
                        bgcolor: "background.default",
                        p: 1
                    }
                }}
            >

                <DialogTitle children={"Contact Seller"} />

                <DialogContent>

                    <DialogContentText id="alert-dialog-slide-description">
                        Let the Seller know you're interested, leave an Email oe Mobile number for the seller to reach back to you.
                        You can also optionally leave message for the seller to see.
                    </DialogContentText>

                    <Stack direction="column" gap={1} mt={2} >
                        <MobileInput
                            number={input.mobile?.number}
                            // @ts-ignore
                            countryCode={input.mobile?.countryCode}
                            handleCountryCode={e => {
                                setInput({
                                    ...input,
                                    // @ts-ignore
                                    mobile: {
                                        ...input.mobile,
                                        countryCode: e.target.value
                                    }
                                })
                            }}
                            handleNumber={e => {
                                setInput({
                                    ...input,
                                    mobile: {
                                        ...input.mobile,
                                        number: e.target.value
                                    }
                                })
                            }}
                        />
                        <TextField
                            required
                            label="Name"
                            variant='standard'
                            value={input.name}
                            onChange={e => {
                                setInput({
                                    ...input,
                                    name: e.target.value
                                })
                            }}
                        />
                        <TextField
                            required
                            label="Subject"
                            variant="standard"
                            value={input.subject}
                            onChange={e => setInput({
                                ...input,
                                subject: e.target.value
                            })}

                        />
                        <TextField
                            required
                            label="Message"
                            variant="standard"
                            value={input.message}
                            onChange={e => setInput({
                                ...input,
                                message: e.target.value
                            })}
                            multiline
                        />
                    </Stack>

                </DialogContent>

                <DialogActions>
                    <Button
                        variant="outlined"
                        sx={{
                            borderColor: "secondary.main", color: "secondary.main", "&:hover": {
                                bgcolor: "secondary.main", color: "text.primary", borderColor: "secondary.main",
                            }
                        }}
                        onClick={handleClose}
                        children="Cancel"
                    />
                    <Button
                        onClick={_handle}
                        children={
                            isInFlight ?
                                <CircularProgress size="small" />
                                : "Submit"
                        }
                        sx={{
                            borderColor: "primary.main",
                            "&:hover": { color: "primary.main", bgcolor: "transparent", borderColor: "primary.main", borderWidth: "1px" }
                        }}
                    />
                </DialogActions>

            </Dialog>
        );
    }

    ShippingInfo = () => {
        return (
            <Stack
                direction="column"
                gap={1}
                sx={{
                    my: 2,
                    "& h1": {},
                    "& p": { mb: 2 }
                }}
            >

                <Typography
                    component="h1"
                    variant="h4"
                    children="Free Shipping"
                />
                <Typography
                    component="p"
                    variant="body1"
                    children=" We're excited to offer free ground shipping on all orders placed within the USA. Not only that, but every order is fully insured, ensuring that your purchase is protected from beginning to end. All orders require adult (+21 year old) signature delivery."
                />

                <Typography
                    component="h1"
                    variant="h4"
                    children="Hassle-Free Exchanges"
                />

                <Typography
                    component="p"
                    variant="body1"
                    children="If for any reason you are not completely satisfied with your order, please contact us within 15 days of delivery. We will be more than happy to process a return or exchange for you. Your satisfaction is our guarantee. Returns are limited to 2 (two) returns per customer per calender year to avoid abuse of our system."
                />

            </Stack>
        )
    }

    ValueText = ({ title, value }: {
        title: string;
        value: any;
    }) => {
        return (
            <Stack direction="row" gap={1} fontSize="1rem" >
                <Typography
                    color="text.secondary"
                    textTransform="capitalize"
                    children={title + ": "} />
                <Typography
                    color="text.primary"
                    children={value ?? " - "}
                />
            </Stack>
        )
    }

    Buttons = ({ allowInquiry }: { allowInquiry: boolean | null | undefined }) => {
        const handleClickOpen = () => {
            this.setState({
                ...this.state,
                openInquiryForm: true
            });
        };

        return (
            <Stack direction="column" spacing={2} sx={{ mt: 2, "& .MuiButton-root": { borderRadius: 2, p: 1 } }}>

                <Button
                    variant="text"
                    sx={{
                        color: "background.default", bgcolor: "primary.main",
                        "&:hover": { color: "text.primary" }
                    }}
                    children="Add to Cart"
                />

                <Button
                    disabled={allowInquiry === false}
                    onClick={handleClickOpen}
                    variant="text"
                    children="Contact Seller"
                    sx={{
                        color: "primary.main", bgcolor: "background.default", borderColor: "primary.main", borderWidth: "1px", borderStyle: "solid",
                        "&:hover": { color: "text.primary", borderColor: "text.primary", }
                    }}
                />

            </Stack>
        )
    }

    AdvancedInfo = ({ children }: PropsWithChildren) => {
        const { openAdvance } = this.state;
        const _toggle_advanced = () => {
            this.setState({
                ...this.state,
                openAdvance: !openAdvance
            })
        };

        return (
            <>
                <Divider />
                <Stack direction="row" p={1} gap={1} justifyContent="center" alignItems="center" >
                    <MessageSharp />
                    <Typography flex={1} display="flex" >
                        Advanced Info
                    </Typography>

                    <IconButton
                        onClick={_toggle_advanced}
                        children={<ChevronRight />}
                        sx={{ transform: openAdvance ? "rotate(90deg)" : "rotate(0deg)" }}
                    />

                </Stack>

                <Collapse
                    in={this.state.openAdvance}
                    children={
                        <Stack
                            direction="column"
                            children={children}
                            py={2}
                            px={1}
                            gap={1}
                        />
                    }
                    sx={{
                        "& .MuiStack-root": {
                            animation: `${slideInFromTop} 300ms ease-in`
                        }
                    }}
                />
                <Divider />
            </>
        )
    }

    ShareButton = () => {
        const shareData = {
            title: 'Diamond Details',
            text: 'Diamond Details',
            url: window.location.href,
        };

        const shareURL = () => {
            try {
                window?.navigator?.share(shareData)
                    .then(() => alert('Shared successfully'))
                    .catch(error => alert('Error sharing:' + error));
            } catch (e) {
                console.log('Web Share API not supported', e);
            }
        };

        return (
            <IconButton
                aria-label="share"
                size="small"
                disableRipple
                sx={{ "&:hover": { bgcolor: "transparent" }, mb: 2 }}
                onClick={shareURL}
            >
                <Share sx={{ color: "primary.main", m: 1 }} />
                {" "}Share
            </IconButton>
        )
    }

    DataLayer = () => {
        const { id } = useParams();
        const { Buttons, ShareButton, AdvancedInfo, ValueText, ShippingInfo, InquiryForm, SuggestMore } = this;
        const data = useLazyLoadQuery<DiamondDetailsQuery>(
            query,
            { id: id ?? "" },
            { fetchPolicy: 'store-or-network' },
        );

        return (
            <Box sx={{ py: 4, px: 2 }} >

                {/* root data grid */}
                <Grid container >

                    <Grid item xs={12} md={6} sx={{
                        p: { xs: 0, md: 2 }
                    }} >
                        <Box
                            component="img"
                            src="/static/diamond.jpg"
                            sx={{
                                display: 'flex',
                                justifyContent: 'center',
                                alignItems: 'center',
                                alignSelf: 'center',
                                height: {
                                    xs: '50vh',
                                    md: "70vh"
                                },
                                width: "100%",
                                objectFit: 'cover',
                            }}
                        />

                        {/* advanced info */}
                        <AdvancedInfo>
                            <ValueText title='depth' value={data?.node?.depth} />
                            <ValueText title='height' value={data?.node?.height} />
                            <ValueText title='width' value={data?.node?.width} />
                            <ValueText title='table' value={data?.node?.table} />
                            <ValueText title='pavilion Angle' value={data?.node?.pavilionAngle} />
                            <ValueText title='girdle Thickness' value={data?.node?.girdleThickness} />
                            <ValueText title='gridle Percentage' value={data?.node?.gridlePercentage} />
                            <ValueText title='crown Height' value={data?.node?.crownHeight} />
                        </AdvancedInfo>
                        {/*  */}

                    </Grid>

                    <Grid item xs={12} md={6} >

                        <Stack direction="column" mb={3} gap={1.5} >
                            <Typography
                                component="h1"
                                variant="overline"
                                color="primary.main"
                                textTransform="uppercase"
                                children={data?.node?.business?.displayName}
                            />
                            <Typography
                                component="h1"
                                variant="h3"
                                color="text.primary"
                                fontWeight="300"
                                children={
                                    data?.node?.caratWeight + " Carat " +
                                    data?.node?.shape + " | " +
                                    data?.node?.cut + " | " +
                                    data?.node?.color + " | " +
                                    data?.node?.clarity
                                }
                            />
                            <Typography
                                variant='subtitle1'
                                color='text.secondary'
                                children={
                                    "Price: " + (data?.node?.currency ?? " - ") + " " + (data?.node?.price ?? " - ")
                                }
                            />
                        </Stack>

                        <Buttons
                            allowInquiry={data.node?.inquiriesEnabled}
                        />

                        {/* base info */}
                        <Stack direction="column" gap={2}
                            sx={{
                                my: 2,
                                "& .MuiTypography-root": {
                                    fontSize: "1rem",
                                    fontWeight: "300",
                                    color: "text.secondary",
                                    "& a": {
                                        color: "primary.main",
                                        textDecoration: "underline",
                                        cursor: "pointer"
                                    }
                                }
                            }}
                        >
                            <Typography variant='subtitle1' children={"SR No: " + (data?.node?.srNo ?? " - ")} />
                            <Typography
                                variant='subtitle1'
                                children={
                                    (data?.node?.cut ?? " - ") + " Cut" + " | " +
                                    (data?.node?.color ?? " - ") + " Color" + " | " +
                                    (data?.node?.clarity ?? " - ") + " | " +
                                    (data.node?.lab ?? "Non") + " Certified"
                                }
                            />
                            <Typography
                                variant='subtitle1'
                                children={
                                    <> Video:{" "}
                                        <a href="https://view.gem360.in/gem360/0703230952-M2960/gem360-0703230952-M2960.html" target='_blank'>Diamond Video</a>
                                    </>
                                }
                            />
                            <Typography
                                variant='subtitle1'
                                children={
                                    <> Certificate Number:{" "}
                                        <a
                                            href={`https://www.gia.edu/report-check?reportno=${data.node?.reportNumber ?? "6455925172"}`}
                                            target='_blank'>{data.node?.reportNumber ?? "6455925172"}</a>
                                    </>
                                }
                            />
                            <Typography
                                variant='subtitle1'
                                children={"Diamond Dimensions (mm): " + (data?.node?.dimensions ?? " - ")}
                            />
                            <Typography
                                variant='subtitle1'
                                children={"Status: " + (data?.node?.status)}
                            />
                            <Typography
                                variant='subtitle1'
                                children={"Comment: " + (data.node?.comment ?? " - ")}
                            />
                            <Divider />
                        </Stack>
                        {/*  */}

                        {/* share button */}
                        <ShareButton />
                        {/*  */}

                    </Grid>

                </Grid>
                {/*  */}

                {/* shipping info and T&C */}
                <ShippingInfo />
                {/*  */}

                {/* inquiry form */}
                <InquiryForm />
                {/*  */}

                {/* See more */}
                <React.Suspense
                    fallback={
                        <Skeleton
                            variant="rectangular"
                            height={200}
                            width={200}
                        />
                    }
                >
                    <SuggestMore
                        frag={data}
                        vars={{
                            first: 4,
                            caratGte: Number(data.node?.caratWeight),
                            caratLt: Number(data.node?.caratWeight) + 0.5,
                            shapeLte: `${ShapeKeys.findIndex(k => k === data.node?.shape) + 2}`,
                            colorLte: `${ColorChoicesKeys.findIndex(k => k === data.node?.color) + 2}`,
                            cutLte: "4",
                            clarityLte: `${ClarityChoicesKeys.findIndex(k => k === data.node?.clarity) + 3}`,
                            fetchMore: Boolean(data.node)
                        }}
                    />
                </React.Suspense>
                {/*  */}

            </Box>
        );

    }

    render() {
        const { DataLayer } = this;
        return (
            <DataLayer />
        );
    }

};

export default DiamondDetails;
