import React from 'react';
import { Avatar, Card, CardActions, CardContent, CardHeader, CardMedia, IconButton, Stack, Tooltip, Typography } from '@mui/material';
import { useFragment } from 'react-relay';
import { DiamondList_data$key } from './__generated__/DiamondList_data.graphql';
import { Bookmark, Info, Message, MoreVert } from '@mui/icons-material';
import { red } from '@mui/material/colors';
import { useNavigate } from 'react-router';

const graphql = require('babel-plugin-relay/macro');

interface DiamondProps {
    data: DiamondList_data$key;
};
const query = graphql`
fragment DiamondList_data on DiamondType{
        id
        caratWeight
        clarity
        cut
        color
        status
        polish
        shape
        business{
            id
            displayName
        }
    }
`;

const TextValue = (props: { label: string, value?: any }) => {
    return (
        <Stack justifyContent="space-between" direction="row" alignItems="center" textAlign="left" >
            <Typography variant="body2" color="text.primary" textTransform="uppercase" >
                {props.label}
            </Typography>
            <Typography variant="body2" color="text.secondary" textAlign="right">
                {props.value ?? " - "}
            </Typography>
        </Stack>
    );
};


export default function DiamondCard(props: DiamondProps) {
    const nav = useNavigate();
    const data = useFragment(
        query,
        props.data
    );

    return (
        <Card
            onClick={() => {
                nav(`/diamond/${data.id}`)
            }}
            sx={{
                minWidth: 300,
                margin: 2,
                borderRadius: 2,
                boxShadow: 0,
                width: {
                    xs: "90%",
                    sm: "40%",
                    md: "30%",
                    lg: "20%",
                },
                transition: "all 0.2s ease-in-out",
                "&:hover": {
                    boxShadow: theme => `0px 0px 9px ${theme.palette.text.primary}`
                }
            }}
        >

            <CardHeader
                avatar={
                    <Avatar sx={{ bgcolor: red[500] }} aria-label="business">
                        {data.business?.displayName?.charAt(0) ?? " - "}
                    </Avatar>
                }
                action={
                    <IconButton aria-label="settings">
                        <MoreVert />
                    </IconButton>
                }
                title={data.business?.displayName?.toLocaleUpperCase() ?? " - "}
                subtitle={data.status ?? " Status Unavailable"}
            />

            <CardMedia
                component="img"
                image="/static/diamond.jpg"
                sx={{
                    objectFit: "cover",
                    height: {
                        xs: 200,
                        md: 140
                    }
                }}
                alt="diam img"

            />

            <CardContent>

                <Stack gap={1}>
                    <TextValue label="Shape" value={data.shape} />
                    <TextValue label="Weight" value={data.caratWeight} />
                    <TextValue label="Clarity" value={data.clarity} />
                    <TextValue label="CUT" value={data.cut} />
                    <TextValue label="Color" value={data.color} />
                    <TextValue label="Polish" value={data.polish} />
                </Stack>

            </CardContent>

            <CardActions sx={{ justifyContent: "space-between", px: 1 }} >

                <Tooltip title="Add to Favorites" placement="top">
                    <IconButton aria-label="Add to favorites">
                        <Bookmark />
                    </IconButton>
                </Tooltip>

                <Tooltip title="Details" placement="top" >
                    <IconButton aria-label="Details">
                        <Info />
                    </IconButton>
                </Tooltip>

                <Tooltip title="Inquire" placement="top" >
                    <IconButton aria-label="Inquire">
                        <Message />
                    </IconButton>
                </Tooltip>
            </CardActions>

        </Card>
    );

};


