import React from 'react';
import { Box, Checkbox, CircularProgress, Divider, FormControl, IconButton, InputLabel, MenuItem, Select, SelectChangeEvent, Stack, Switch, TextField, Typography } from '@mui/material';
import { slideInFromBottom } from '../animations/text';
import { useMutation } from 'react-relay';
import { Login } from '@mui/icons-material';
import { AuthMutation } from './__generated__/AuthMutation.graphql';

const graphql = require('babel-plugin-relay/macro');

export const countryCodes = [
    { value: '+91', label: 'India' },
    { value: '+1', label: 'United States' },
    { value: '+44', label: 'United Kingdom' },
    { value: '+971', label: 'United Arab Emirates' },
    { value: '+966', label: 'Saudi Arabia' },
    { value: '+65', label: 'Singapore' },
    { value: '+60', label: 'Malaysia' },
    { value: '+62', label: 'Indonesia' },
    { value: '+63', label: 'Philippines' },
    { value: '+66', label: 'Thailand' },
    { value: '+84', label: 'Vietnam' },
    { value: '+86', label: 'China' },
    { value: '+81', label: 'Japan' },
    { value: '+82', label: 'South Korea' },
    { value: '+852', label: 'Hong Kong' },
    { value: '+853', label: 'Macau' },
    { value: '+886', label: 'Taiwan' },
    { value: '+90', label: 'Turkey' },
    { value: '+33', label: 'France' },
    { value: '+34', label: 'Spain' },
    { value: '+39', label: 'Italy' },
    { value: '+49', label: 'Germany' },
    { value: '+31', label: 'Netherlands' },
    { value: '+41', label: 'Switzerland' },
    { value: '+43', label: 'Austria' },
    { value: '+32', label: 'Belgium' },
    { value: '+30', label: 'Greece' },
    { value: '+351', label: 'Portugal' },
    { value: '+46', label: 'Sweden' },
    { value: '+47', label: 'Norway' },
    { value: '+45', label: 'Denmark' },
    { value: '+358', label: 'Finland' },
    { value: '+420', label: 'Czech Republic' },
    { value: '+48', label: 'Poland' },
    { value: '+36', label: 'Hungary' }
];

interface AuthProps {
    next?: () => void;
};

const query = graphql`
    mutation AuthMutation($i:MobileAuthenticationInput!){
        mobileAuth(input:$i){
            token
            id
            error
            success
        }
    }
`;

export const MobileInput = (
    { countryCode = "+91", number, handleCountryCode, handleNumber }: {
        countryCode?: string;
        number?: string;
        handleCountryCode: (event: SelectChangeEvent<any>) => void;
        handleNumber: (event: React.ChangeEvent<HTMLInputElement>) => void;
    }
) => {
    return (
        <Stack
            sx={{ flexDirection: "row", mt: "1rem", bgcolor: 'background.paper', borderRadius: "1rem", p: 2 }}
            gap={2}
            justifyContent="space-between"
        >
            <FormControl variant="standard" sx={{ minWidth: "70px", backdropFilter: "blur(2px)", backgroundColor: "transparent" }}>
                <InputLabel id="country-code-label">
                    {countryCode ? countryCodes.find((c) => c.value === countryCode)?.label : "Country Code"}
                </InputLabel>
                <Select
                    labelId="country-code-label"
                    id="country-code"
                    value={countryCode}
                    onChange={handleCountryCode}
                    label="Country Code"
                    fullWidth
                    required
                    children={countryCodes.map((c) => (<MenuItem key={c.label} value={c.value} children={c.value} />))}
                />
            </FormControl>
            <TextField
                label="Mobile Number"
                variant="standard"
                fullWidth
                value={number}
                onChange={handleNumber}
                itemType="tel"
                type="tel"
                inputMode="tel"
                required
                sx={{ backgroundColor: "transparent" }}
            />
        </Stack>
    )
};

function Auth({ next }: AuthProps) {
    const [login, isLoading] = useMutation<AuthMutation>(query)
    const [countryCode, setCountryCode] = React.useState<string>('+91');
    const [number, setNumber] = React.useState<string>('');
    const [otpId, setOtpId] = React.useState<string>('');
    const [otp, setOtp] = React.useState<string>('');
    const [whatsappNumber, setWhatsappNumber] = React.useState<boolean>(false);
    const [terms, setTerms] = React.useState<boolean>(false);

    const handleOtpID = (id: string) => {
        setOtpId(id);
    };

    const handleOtp = (event: React.ChangeEvent<HTMLInputElement>) => {
        setOtp(event.target.value);
    };

    const handleNumber = (event: React.ChangeEvent<HTMLInputElement>) => {
        setNumber(event.target.value);
    };

    const handleWhatsappNumber = (event: React.ChangeEvent<HTMLInputElement>) => {
        if (countryCode === '+91') {
            setWhatsappNumber(event.target.checked);
        }
        else {
            alert("Sorry SMS is not supported in your country, but don't worry Whatsapp works just fine 😉.")
        }
    };

    const handleCountryCode = (event: SelectChangeEvent) => {
        setCountryCode(event.target.value);
        if (event.target.value !== '+91') { setWhatsappNumber(true); }
    };

    const handleSubmit = () => {

        if (!terms) {
            return alert("Please agree to the terms and conditions to continue.");
        }

        else {

            if (otpId) {
                return login({
                    variables: {
                        i: {
                            "verification": {
                                "otp": otp,
                                "id": otpId
                            }
                        }
                    },
                    onCompleted: (data) => {
                        if (data?.mobileAuth?.success) {
                            data?.mobileAuth?.token && localStorage.setItem("token", data?.mobileAuth?.token);
                            next && next()
                        } else {
                            alert(data?.mobileAuth?.error);
                        }
                    }
                })
            }

            else if (number && number.length > 3) {
                return login({
                    variables: {
                        i: {
                            mobile: {
                                "countryCode": countryCode,
                                "number": number
                            },
                            "whatsapp": whatsappNumber
                        }
                    },
                    onCompleted: (data) => {
                        if (data?.mobileAuth?.success) {
                            data?.mobileAuth?.id && handleOtpID(data?.mobileAuth?.id);
                        } else {
                            return alert(data?.mobileAuth?.error);
                        }
                    },
                    onError: (error) => {
                        return alert(error?.message);
                    }
                });
            }

            else {
                alert("Please enter a valid number");
            }
        }
    };

    React.useEffect(() => {
        let keylistener = (e: KeyboardEvent) => {
            if (e.key === 'Enter') {
                handleSubmit();
            }
        }
        window.addEventListener('keydown', keylistener);
        return () => {
            window.removeEventListener('keydown', keylistener);
        }
    }, [
        countryCode,
        number,
        otp,
        otpId,
        whatsappNumber,
        terms
    ]);

    return (
        <Box
            sx={{
                animation: `${slideInFromBottom} 500ms ease forwards`,
                overflowY: "scroll",
                height: "100%",
                width: "100%",
                justifyContent: "center",
                alignItems: "center",
            }}
        >

            <Stack gap={2} sx={{ minHeight: "400px", alignSelf: "center", height: "100%" }} direction="column" p={2} justifyContent="space-around" >

                {otpId ?
                    <Box sx={{
                        my: "auto",
                        width: "100%",
                        alignSelf: "center",
                        textAlign: "center",
                        bgcolor: 'background.paper',
                        borderRadius: "1rem",
                        p: 2
                    }}>
                        <TextField
                            label="OTP"
                            variant="standard"
                            value={otp}
                            onChange={handleOtp}
                            itemType='otp'
                            required
                        />
                    </Box>
                    :
                    <MobileInput
                        countryCode={countryCode}
                        number={number}
                        handleCountryCode={handleCountryCode}
                        handleNumber={handleNumber}
                    />
                }


                <Stack
                    sx={{ display: otpId ? "none" : "flex" }}
                    direction="row" mt="auto" spacing={2} alignItems="center" justifyContent="space-between" >
                    <Typography variant="body1" >
                        Login with Whatsapp
                    </Typography>
                    <Switch
                        checked={whatsappNumber}
                        onChange={handleWhatsappNumber}
                    />
                </Stack>

                <Divider />

                <Stack
                    sx={{
                        display: otpId ? "none" : "flex"
                    }}
                    direction="row" spacing={2} alignItems="center" justifyContent="flex-start" >
                    <Checkbox value={terms} onChange={e => setTerms(e.target.checked)} />
                    <Typography
                        variant="subtitle2"
                        sx={{
                            color: "text.secondary",
                            animation: `${slideInFromBottom} 500ms ease forwards`,
                        }}
                    >
                        I agree to the Terms and Conditions. {" "}
                        <a href="https://www.beyondigital.agency/terms-and-conditions" target="_blank" rel="noreferrer" style={{ color: "inherit" }}>
                            Read More
                        </a>
                    </Typography>
                </Stack>

                <IconButton
                    sx={{
                        backgroundColor: theme => theme.palette.primary.main,
                        color: theme => theme.palette.primary.contrastText,
                        borderRadius: "1rem",
                        boxShadow: theme => theme.shadows[10],
                        my: 1,
                        py: {
                            xs: 1,
                            md: 2
                        },
                        px: 2,
                        animation: `${slideInFromBottom} 500ms ease forwards`,
                        justifyContent: "space-between",
                        "&:hover": {
                            backgroundColor: theme => theme.palette.primary.dark,
                        }
                    }}
                    onClick={handleSubmit}
                    disabled={isLoading}
                >
                    <Typography
                        variant="overline"
                        flex={1}
                        children={
                            isLoading ? "Talking to the server..." :
                                otpId ? "Verify OTP" : "Send OTP"
                        }
                    />

                    {
                        isLoading ?
                            <CircularProgress size={24} /> :
                            <Login />
                    }

                </IconButton>

            </Stack>

        </Box>
    );

};

export default Auth;
